import { computed, ref } from 'vue';
import LeftNav from './components/leftnav.vue';
import { useStore } from 'vuex';
import xiuweiJson from '@/assets/data/xiuwei.json';
import jinriyunshiJson from '@/assets/data/jinriyunshi.json';
export default {
  name: 'Home',
  components: {
    LeftNav
  },

  setup() {
    const store = useStore(); //存储点击的是哪个左侧导航 1是基本 2是攻防 3是五行

    const leftnavActive = ref(1);
    const user = computed(() => {
      return store.state.user.userInfo;
    }); //处理用户基本信息

    const userInfoComputed = () => {
      //处理修为境界
      xiuweiJson.forEach(xiuweiItem => {
        //将字符串切成两个字符串数组 "1001-2000"  ['1001','2000']
        const arr = xiuweiItem.number.split('-'); //与json中的数据进行比较

        if (parseInt(user.value.xiuwei) >= parseInt(arr[0]) && parseInt(user.value.xiuwei) <= parseInt(arr[1])) {
          user.value.jingjie = xiuweiItem.name;
        }
      }); //处理今日运势

      jinriyunshiJson.forEach(yunshiItem => {
        //根据id找到对应运势
        if (user.value.jinriyunshi === yunshiItem.id) {
          user.value.jinriyunshi = yunshiItem.name;
        }
      });
    };

    userInfoComputed(); //更改左侧导航触发事件

    const changeLeftNav = navNow => {
      //存储当前更改的导航
      leftnavActive.value = navNow;
    };

    return {
      leftnavActive,
      changeLeftNav,
      user
    };
  }

};