import { ref } from 'vue';
export default {
  name: 'LeftNav',

  setup(props, {
    emit
  }) {
    //当前点击的导航
    let leftnavActive = ref(1); //点击事件

    const btnClick = navNow => {
      //存储当前导航
      leftnavActive.value = navNow; //通知父组件

      emit('changeLeftNav', navNow);
    };

    return {
      leftnavActive,
      btnClick
    };
  }

};